<template>
  <div class="login-container">
    <div class="login-box">
      <h2>–í—Ö–æ–¥</h2>
      <form @submit.prevent="login">
        <input type="text" v-model="username" placeholder="–õ–æ–≥–∏–Ω" required />
        <input type="password" v-model="password" placeholder="–ü–∞—Ä–æ–ª—å" required />
        <button type="submit">–í–æ–π—Ç–∏</button>
      </form>
      <p class="register-link">–ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? <router-link to="/register">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</router-link></p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    async login() {
      try {
        const response = await fetch('/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ username: this.username, password: this.password })
        });
        const data = await response.json();
        if (response.ok) {
          localStorage.setItem('token', data.access_token);
          this.$router.push('/dashboard');
        } else {
          alert(data.message);
        }
      } catch (error) {
        console.error('–û—à–∏–±–∫–∞ –≤—Ö–æ–¥–∞:', error);
      }
    }
  }
};
</script>

<style scoped>
/* –§–æ–Ω –≤ —Å—Ç–∏–ª–µ Google */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to bottom, #f8f9fa, #e9ecef);
}

/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Ñ–æ—Ä–º—ã */
.login-box {
  background: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 360px;
  width: 100%;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

/* –ü–æ–ª—è –≤–≤–æ–¥–∞ */
input {
  width: 90%;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

/* –ö–Ω–æ–ø–∫–∞ –≤—Ö–æ–¥–∞ */
button {
  width: 100%;
  padding: 12px;
  background-color: #4285f4;
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –∫–Ω–æ–ø–∫–∏ */
button:hover {
  background-color: #357ae8;
}

/* –°—Å—ã–ª–∫–∞ –Ω–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é */
.register-link {
  margin-top: 15px;
  font-size: 14px;
}
</style>
